import React from 'react';

import 'bootstrap';

import 'bootstrap/dist/css/bootstrap.css';

export const LocationComponent = ({ name, clickEvent }) => {
	return (
		<div
			id="location_c"
			onClick={e => {
        e.preventDefault();
        if(clickEvent)
				clickEvent(name);
			}}
			className="btn btn-primary"
		>
			{name}
		</div>
	);
};
