import React from 'react';
import './style/content.css';

import { LocationComponent } from './location_comp';
import { TableShow } from './tabelShow';

export default class TypeofWork extends React.Component {
	constructor() {
		super();
		this.state = {
			locations: [{ name: "Diesel Record" }, { name: "Suppliers Bill" }, { name: 'Debtors Bill' }, { name: 'Imprest Report' }],
			showTable: false,
			currentChoice: '',
		};

		this.showTable = this.showTable.bind(this);
		this.offTable=this.offTable.bind(this);
		this.backHome= this.backHome.bind(this);
	}
	showTable(name) {
		this.setState({ currentChoice: name, showTable: true });
	}
	offTable(){
		this.setState({showTable: false });
	}
	backHome(){
		this.offTable();
		this.props.backHome();
	}
	
	render() {
		return (
			<React.Fragment>
				{!this.state.showTable && (
					<center>
						<div className="content_component">
							<p className="card-header_"><span id="type_d"><button id="typeBtn" className="btn btn-primary" onClick={this.props.backHome}>Back</button>{this.props.locationName + '>choose Type of Work'}</span> </p>
							
							{this.state.locations.map((val, num) => (
								<LocationComponent clickEvent={this.showTable} key={num} name={val.name} />
							))}
						</div>
					</center>
				)}
				{this.state.showTable && (
					<TableShow location={this.props.locationName} back={this.offTable} backHome={this.backHome} type={this.state.currentChoice} />
				)}
			</React.Fragment>
		);
	}
}
