import React from 'react';
import './style/content.css';

import { LocationComponent } from './location_comp';
import TypeOfWork from './typeofWork'; 


export default class Content extends React.Component {
	constructor() {
		super();
		this.state = {
			locations: [{ name: "Varanisi(U.P-1)" }, { name: "Sultanpur(U.P-2)" }, { name: "Jhabua(M.P)" }, { name: "Solan(H.P)" },{name:"Punjab"}],
           changeToTypes:false, 
           currentActive:"",
        };
        
		this.showType=this.showType.bind(this);
		this.backHome=this.backHome.bind(this);
	};

 
    showType(name){
        this.setState({changeToTypes:true,currentActive:name});
	}
	backHome(){
        this.setState({changeToTypes:false});

	}

	render() {
		return (
            <React.Fragment>
			{!this.state.changeToTypes&&<center>
				<div className="content_component">
                <p className="card-header_">Select work Location</p>
					{this.state.locations.map((val,index)=> (
						<LocationComponent key={index} clickEvent={this.showType} name={val.name} />
					))}
				</div>
			</center>}
            {this.state.changeToTypes&&<TypeOfWork  backHome={this.backHome} locationName={this.state.currentActive}/>}
            </React.Fragment>
		);
	}
}
