import React from 'react';
import { FilePond } from 'react-filepond';
import 'filepond/dist/filepond.min.css';
import '@fortawesome/fontawesome-free/css/all.css';
import jquery from 'jquery';


var locationMapper={
	"Varanisi(U.P-1)":"locationA",
	"Sultanpur(U.P-2)":"locationB",
	"Jhabua(M.P)":"locationC",
	"Solan(H.P)" :"locationD",
	"Punjab":"locationE",
};

var typeMapper={
	"Diesel Record":"typeA",
	"Suppliers Bill":"typeB",
	"Debtors Bill":"typeC",
	"Imprest Report":"typeD",

};

const TableComp = props => {
	return (
		<table className="table table-hover" id="tbl">
			<thead>
				<tr>
					<th>Date</th>
					<th>Name</th>
					<th>Download Link</th>
				</tr>
			</thead>
			<tbody>{props.children}</tbody>
		</table>
	);
};

const TableRow = ({ date, fileName, downloadLink }) => {
	return (
		<tr>
			<td>{date}</td>
			<td>{fileName}</td>
			<td className="btn btn-success">
				<a href={downloadLink}>Download</a>
			</td>
		</tr>
	);
};


class TableFilter extends React.Component{

	constructor(){
		super();
		this.state={
			value:"",
		}
		this.ref=null;
	}

	onChangeEvent(e){
		this.setState({value:this.ref&&this.ref.value});
	}

	render(){
		return (
			<React.Fragment>
			  <input className="form-control" id="filterBr"  ref={r=>this.ref=r} type="text" value={this.state.value} placeholder="search for file..." onChange={this.onChangeEvent.bind(this)}/>
			  <Tables data={this.props.data} location={this.props.location} pattern={this.state.value}/>
			</React.Fragment>
		);
	}
}
var Tables = ({ data, location ,pattern}) => {

	let list = [];
	data.forEach((val ,index)=>{
		if(pattern.length!==0&&pattern!==" "){
		if(val.name.search(pattern)>-1){
			list.push(<TableRow
				key={index}
				date={val.date}
				fileName={val.name}
				downloadLink={'/download/' + locationMapper[location] + '/' + val.workType + '/' + val.name}
			/>);
		}
	}else{

		list.push(<TableRow
			key={index}
			date={val.date}
			fileName={val.name}
			downloadLink={'/download/' + locationMapper[location] + '/' + val.workType + '/' + val.name}
		/>);
	}
	});

	return (
		<TableComp>
			{list}
		</TableComp>
	);
};

const UploadComponent = ({ location, type }) => {
	return (
		<React.Fragment>
			<p className="card-header" id="upload_msg">
				Upload files by dragging files or folders below in white area or by selecting browse option .
			</p>
			<FilePond allowMultiple={true} chunkUpload={false} server={'https://regal-util.herokuapp.com/uploads/' + locationMapper[location] + '/' + typeMapper[type] + '/'} />
		</React.Fragment>
	);
};

const UploadButton = ({ showUploadComponent }) => {
	return (
		<button onClick={showUploadComponent} className="btn btn-success" style={{ width: '100%' }}>
			<i className="fa fa-upload"></i>Upload Files
		</button>
	);
};
export class TableShow extends React.Component {
	constructor() {
		super();
		this.state = {
			data: [
				{ name: 'files-fa', date: 'Date' },
				{ name: 'fio', date: 'Date' },
				{ name: 'C.wgfli', date: 'Date' },
				{ name: 'D.name', date: 'Date' },
				{ name: 'Fdd,wttdf', date: 'Date' },
			],
			status:'Loading',
			phoneView: window.innerWidth > 650 ? false : true,
		};

		this.refresh = this.refresh.bind(this);
		this.showUploadComponent = this.showUploadComponent.bind(this);
	}

	refresh() {
		this.setState({ status: 'Loading' });

		jquery.getJSON('/fetchReq/' + locationMapper[this.props.location] + '/' + typeMapper[this.props.type], jsonData => {
			console.log(jsonData);
			this.setState({ status: 'Done', data: jsonData.data });
		});
	}
	componentDidMount() {
		jquery.getJSON('/fetchReq/' + locationMapper[this.props.location] + '/' + typeMapper[this.props.type], jsonData => {
			console.log(jsonData);
			this.setState({ status: 'Done', data: jsonData.data });
		});
	}

	showUploadComponent() {
		this.setState({ phoneView: false });
	}

	render() {
		return (
			<React.Fragment>
				<div className="" id="tableShow">
					<span className="uploadComponent">
						{!this.state.phoneView && (
							<UploadComponent location={this.props.location} type={this.props.type} />
						)}
						{this.state.phoneView && <UploadButton showUploadComponent={this.showUploadComponent} />}
					</span>
					<p className="tableDiscription">
						<span id="disc">{this.props.location + ' > ' + this.props.type}</span>

						<span className="btn btn-success" onClick={this.props.back}>
							Back
						</span>
						<span className="btn btn-primary" onClick={this.props.backHome}>
							Home
						</span>
						<span className="btn btn-info" onClick={this.refresh}>
							Refresh
						</span>
					</p>
					{this.state.status === 'Done' && <TableFilter data={this.state.data} location={this.props.location}/>}

					{this.state.status === 'Loading' && (
						<React.Fragment>
							<div id="spinners">
								<div
									className="spinner-grow text-dark"
									style={{ width: '2rem', height: '2rem' }}
									role="status"
								>
									<span className="sr-only">Loading...</span>
								</div>
								<div
									className="spinner-grow text-success"
									style={{ width: '2rem', height: '2rem' }}
									role="status"
								>
									<span className="sr-only">Loading...</span>
								</div>
								<div
									className="spinner-grow text-info"
									style={{ width: '2rem', height: '2rem' }}
									role="status"
								>
									<span className="sr-only">Loading...</span>
								</div>
								<div
									className="spinner-grow text-danger"
									style={{ width: '2rem', height: '2rem' }}
									role="status"
								>
									<span class="sr-only">Loading...</span>
								</div>
								<div
									className="spinner-grow text-primary"
									style={{ width: '2rem', height: '2rem' }}
									role="status"
								>
									<span className="sr-only">Loading...</span>
								</div>
							</div>
						</React.Fragment>
					)}
				</div>
			</React.Fragment>
		);
	}
}
